/* 
	variables 
*/
/* Colour palette: https://colorhunt.co/palette/118591 */
/*
	mixins
*/
/*
	basic set up
*/
* {
  padding: 0;
  margin: 0; }

body {
  height: 100%;
  font-family: "Sunflower", sans-serif;
  font-size: 1em;
  color: #062743;
  text-align: center;
  background-color: #f9f9f9; }
  @media (min-width: 600px) {
    body {
      display: grid;
      grid-template-areas: "instructions instructions" "options container" "highscores container" "result result";
      grid-template-columns: 1fr, 2fr; } }
  @media (min-width: 900px) {
    body {
      grid-template-areas: ". instructions ." "options container highscores" ". result .";
      grid-template-columns: 1fr, 2fr, 1fr; } }

/*
	instructions - we want them to be read first, so need to be top of herarchy visually
*/
.instructions {
  grid-area: instructions;
  padding-top: 1rem;
  padding-bottom: 1rem;
  font-size: 1.3rem; }

/*
	select dropdown - making it prettier than the default
	https://www.filamentgroup.com/lab/select-css.html
*/
.select-shape-container {
  grid-area: options; }

.select-shape {
  padding: 0.2rem;
  padding-right: 1.6rem;
  font-family: "Sunflower", sans-serif;
  font-size: 1em;
  color: #062743;
  box-shadow: 0 1px 0 1px rgba(0, 0, 0, 0.04);
  border: 1px solid #062743;
  border-radius: .2em;
  background-color: #f9f9f9;
  background: url("chevron-down-outline.svg");
  background-size: 1.5rem 1.5rem;
  background-position: right center;
  background-repeat: no-repeat;
  appearance: none; }

.select-shape::-ms-expand {
  display: none; }

option {
  font-size: 0.9rem; }

/*
	the shape itself
*/
.shape-container {
  grid-area: container;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 300px;
  min-width: 300px;
  margin-top: 1rem;
  margin-bottom: 1rem;
  padding-left: 1rem;
  padding-right: 1rem; }

.shape {
  visibility: hidden;
  stroke: none;
  fill: #ff7a8a; }

.helper-svg {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto; }

.helper {
  visibility: hidden;
  fill: #062743; }

/*
	result text and reset button
*/
.result {
  grid-area: result;
  display: flex;
  flex-direction: column;
  padding-top: 1rem;
  padding-bottom: 1rem;
  font-weight: bold; }

.reset-container {
  padding-top: 1rem;
  padding-bottom: 1rem; }

.reset {
  box-shadow: 0 5px 12px -5px rgba(0, 0, 0, 0.5);
  border-radius: 0.2em;
  cursor: pointer;
  padding: 0.5em 1.5em;
  font-size: 0.9rem;
  color: #f9f9f9;
  background-color: #2170b3;
  border: #2170b3;
  visibility: hidden; }
  .reset:hover, .reset:active {
    background-color: #195588; }
  .reset:active {
    top: 2px; }

/*
	high scores
*/
.highscores-container {
  grid-area: highscores;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 1rem;
  padding-bottom: 1rem;
  color: #2170b3;
  text-align: left; }
  @media (min-width: 600px) {
    .highscores-container {
      align-self: center; } }

.highscore {
  padding-top: 0.2rem;
  padding-bottom: 0.2rem; }

.highscore-icon {
  padding-right: 0.2rem;
  fill: #2170b3; }

.score {
  visibility: hidden; }

.clear-container {
  padding-top: 0.5rem; }

.clear {
  box-shadow: 0 5px 12px -5px rgba(0, 0, 0, 0.5);
  border-radius: 0.2em;
  cursor: pointer;
  padding: 0.5em 1em;
  font-size: 0.8rem;
  color: #062743;
  background-color: #f9f9f9;
  border: #f9f9f9; }
  .clear:hover, .clear:active {
    background-color: #e0e0e0; }
  .clear:active {
    top: 2px; }
  .clear:disabled {
    cursor: auto;
    pointer-events: none;
    opacity: 0.5; }

/*
	Confetti
*/
/*Downloaded from https://www.codeseek.co/moonglows76/css-confetti-BRVrgZ */
.confetti-container {
  width: 100%;
  height: 100%;
  display: none; }

.confetti {
  width: 15px;
  height: 15px;
  background-color: #f2d74e;
  position: absolute;
  left: 50%;
  -webkit-animation: confetti 5s ease-in-out -2s infinite;
  animation: confetti 5s ease-in-out -2s infinite;
  -webkit-transform-origin: left top;
  transform-origin: left top; }

.confetti:nth-child(1) {
  background-color: #f2d74e;
  left: 10%;
  -webkit-animation-delay: 0;
  animation-delay: 0; }

.confetti:nth-child(2) {
  background-color: #95c3de;
  left: 20%;
  -webkit-animation-delay: -5s;
  animation-delay: -5s; }

.confetti:nth-child(3) {
  background-color: #ff9a91;
  left: 30%;
  -webkit-animation-delay: -3s;
  animation-delay: -3s; }

.confetti:nth-child(4) {
  background-color: #f2d74e;
  left: 40%;
  -webkit-animation-delay: -2.5s;
  animation-delay: -2.5s; }

.confetti:nth-child(5) {
  background-color: #95c3de;
  left: 50%;
  -webkit-animation-delay: -4s;
  animation-delay: -4s; }

.confetti:nth-child(6) {
  background-color: #ff9a91;
  left: 60%;
  -webkit-animation-delay: -6s;
  animation-delay: -6s; }

.confetti:nth-child(7) {
  background-color: #f2d74e;
  left: 70%;
  -webkit-animation-delay: -1.5s;
  animation-delay: -1.5s; }

.confetti:nth-child(8) {
  background-color: #95c3de;
  left: 80%;
  -webkit-animation-delay: -2s;
  animation-delay: -2s; }

.confetti:nth-child(9) {
  background-color: #ff9a91;
  left: 90%;
  -webkit-animation-delay: -3.5s;
  animation-delay: -3.5s; }

.confetti:nth-child(10) {
  background-color: #f2d74e;
  left: 100%;
  -webkit-animation-delay: -2.5s;
  animation-delay: -2.5s; }

@-webkit-keyframes confetti {
  0% {
    -webkit-transform: rotateZ(15deg) rotateY(0deg) translate(0, 0);
    transform: rotateZ(15deg) rotateY(0deg) translate(0, 0); }
  25% {
    -webkit-transform: rotateZ(5deg) rotateY(360deg) translate(-5vw, 20vh);
    transform: rotateZ(5deg) rotateY(360deg) translate(-5vw, 20vh); }
  50% {
    -webkit-transform: rotateZ(15deg) rotateY(720deg) translate(5vw, 60vh);
    transform: rotateZ(15deg) rotateY(720deg) translate(5vw, 60vh); }
  75% {
    -webkit-transform: rotateZ(5deg) rotateY(1080deg) translate(-10vw, 80vh);
    transform: rotateZ(5deg) rotateY(1080deg) translate(-10vw, 80vh); }
  100% {
    -webkit-transform: rotateZ(15deg) rotateY(1440deg) translate(10vw, 110vh);
    transform: rotateZ(15deg) rotateY(1440deg) translate(10vw, 110vh); } }

@keyframes confetti {
  0% {
    -webkit-transform: rotateZ(15deg) rotateY(0deg) translate(0, 0);
    transform: rotateZ(15deg) rotateY(0deg) translate(0, 0); }
  25% {
    -webkit-transform: rotateZ(5deg) rotateY(360deg) translate(-5vw, 20vh);
    transform: rotateZ(5deg) rotateY(360deg) translate(-5vw, 20vh); }
  50% {
    -webkit-transform: rotateZ(15deg) rotateY(720deg) translate(5vw, 60vh);
    transform: rotateZ(15deg) rotateY(720deg) translate(5vw, 60vh); }
  75% {
    -webkit-transform: rotateZ(5deg) rotateY(1080deg) translate(-10vw, 80vh);
    transform: rotateZ(5deg) rotateY(1080deg) translate(-10vw, 80vh); }
  100% {
    -webkit-transform: rotateZ(15deg) rotateY(1440deg) translate(10vw, 110vh);
    transform: rotateZ(15deg) rotateY(1440deg) translate(10vw, 110vh); } }
